<template>
  <v-container fluid>
    <v-card>
      <v-card-text v-if="!isTrue">
        <v-text-field
          v-model="password"
          type="password"
        />
        <v-btn color="success" @click="login()">Login</v-btn>
      </v-card-text>
      <v-card-text v-else>
        <v-data-table
          :headers="headers"
          :items="items"
          hide-actions
          class="elevation-1"
        >
          <template slot="items" slot-scope="props">
            <td>{{props.item.wallet}}</td>
            <td>{{props.item.date}}</td>
            <td>{{props.item.amount}}</td>
            <td>
              <v-btn color="success" @click="paid(props.item.id)">Paid</v-btn>
            </td>
          </template>
        </v-data-table> 
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    password: '',
    isTrue: false,
    headers: [
      { text: 'Wallet', sortable: false },
      { text: 'Date', sortable: false },
      { text: 'Amount', sortable: false }
    ],
    items: []
  }),
  methods: {
    login () {
      axios.post('/admin/login', { pass: this.password }).then((res) => {
        if (res.data.success) {
          this.isTrue = true
          for (let history of res.data.message) {
            this.items.push({
              wallet: history.wallet.wallet,
              date: history.date,
              amount: history.amount,
              id: history.id
            })
          }
        }
      })
    },
    paid (id) {
      axios.post('/admin/paid', { id }).then((res) => {
        console.log(res)
      })
    }
  }
}
</script>

